body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
  
  .columns {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  
  .column {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: justify;
        align-content: space-between;
  }
  .lastColumn {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: distribute;
        align-content: space-around;
  }
  
  .heading {
    margin: auto;
    background: lightskyblue;
    border: 2px solid white;
    color: black;
    width: 100%;
    padding: 4px 5px;
  }
  
  .lastHeading {
    background: lightskyblue;
    border: 2px solid blue;
    color: black;
    width: 100%;
    padding: 4px 5px;
  }
  
  .match {
    margin: auto;
    background: #000;
    border: 1px solid white;
    color: #fff;
    padding: 5px 5px;
    width: 100%;
    text-transform: uppercase;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  
  .scores {
      float: right;
      font-size: 14px;
  }
  
  .set {
      min-width: 20px;
      text-align: center;
  }
  
  sup {
    position: absolute;
      top: -6px;
    font-size: 10px;
        line-height: 0;
      position: relative;
      vertical-align: baseline;
  }
  
  .player {
      font-size: 12px;
      width: 96%;
  }
  
  
  div {
    float: left;
    display: block;
  }
  
  .seed {
    margin-left: 5px;
  }
  
  
  .star {
      display: inline-block;
      background-image: url(images/standard.png);
      background-position: 0 -164px;
      background-repeat: no-repeat;
      height: 12px;
      width: 18px;
      margin-left: 5px;
  }
  
  .winner {
      display: inline-block;
      background-image: url(images/standard.png);
      background-position: 0 -1191px;
      background-repeat: no-repeat;
      height: 12px;
      width: 18px;
      margin-left: 5px;
  }
  
  .tab0 {
    width: 18px;
    background: black;
    color: black;
    padding: 1px 1px;
    margin-right: 2px;
  }
  
  .tab1 {
    width: 18px;
    background: orange;
    padding: 1px 1px;
    margin-right: 2px;
  }
  
  .tab2 {
    width: 18px;
    background: lawngreen;
    padding: 1px 1px;
    margin-right: 2px;
  }
  
  .tab3 {
    width: 18px;
    color: red;
    background: red;
    padding: 1px 1px;
    margin-right: 2px;
  }
  
  .flag {
      display: inline-block;
      background-image: url(images/flags-standard.png);
      background-position: 0 -440px;
      background-repeat: no-repeat;
      height: 12px;
      width: 18px;
     margin-left: 10px;
     margin-right: 5px;
  }
  
